$background: #acacac;
$background_: rgba(5, 7, 10, 0.4);
$background_block: #45423f;
$background_color: rgba(0, 0, 0, 0.554);
$background_color_second: rgba(0, 0, 0, 1);
$background_color_fourth: rgba(5, 7, 10, 0.4);
$color: hsl(0, 0%, 100%);
$color_third: rgb(148, 160, 184);
$color_second: #242222;
$corporate_color: #1a348e;
$corporate_color_second: #1976d2;
$second_corporate_color: #003f5f;

@mixin blockContent(
  $width,
  $height,
  $margin,
  $display,
  $alignItems,
  $justifyContent
) {
  width: $width;
  height: $height;
  margin: $margin;
  display: $display;
  align-items: $alignItems;
  justify-content: $justifyContent;
}

@mixin text($color, $fontSize, $fontWeight, $lineHeight, $textAlign) {
  color: $color;
  font-size: $fontSize;
  font-weight: $fontWeight;
  line-height: $lineHeight;
  text-align: $textAlign;
  font-family: "Roboto", sans-serif;
}
@mixin modalFon {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

@mixin data_entry_form(
  $width,
  $height,
  $color,
  $fontSize,
  $padding,
  $border,
  $borderRadius
) {
  width: $width;
  height: $height;
  color: $color;
  font-size: $fontSize;
  padding: $padding;
  border: 1px solid $border;
  border-radius: $borderRadius;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  font-family: "Rubik Mono One", sans-serif;
}
