@import "../../../styles/Main.scss";

.loader {
  width: 100%;
  height: 100vh;
  flex-direction: column;
  background-color: $corporate_color;
  overflow: hidden;

  &__wrapper {
    @include blockContent(1200px, 100vh, auto, flex, center, space-around);
    flex-direction: column;
    overflow: hidden;
    @media (max-width: 1300px) {
      width: 95%;
    }
  }

  &__block {
    display: flex;
  }

  &__item {
    background-color: #1976d2;
    border-radius: 4px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }

  &__block_button {
    display: flex;
    width: 100%;
    padding: 20px;
    align-items: center;
    justify-content: end;
  }

  &__block_button_second {
    display: flex;
    width: 100%;
    padding: 20px;
    align-items: center;
    justify-content: left;
  }

  &__item_button {
    width: 100px;
    height: 45px;
    border-radius: 4px;
    background-color: #1976d2;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }

  &__block_table {
    width: 100%;
    height: auto;
    margin: auto 0;
    padding-bottom: 30px;
    background-color: #1d7fe218;
  }

  &__item {
    width: 80%;
    height: 150px;
    margin: 60px auto 0 auto;
    border-radius: 4px;
    background-color: #1976d2;
  }

  &__animation {
    width: 48px;
    height: 48px;
    top: 10%;
    position: absolute;
  }

  &__animation ::before,
  .loader__animation::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 48em;
    height: 48em;
    background-image: radial-gradient(circle 10px, #1976d2 100%, transparent 0),
      radial-gradient(circle 10px, #1976d2 100%, transparent 0),
      radial-gradient(circle 10px, #1976d2 100%, transparent 0),
      radial-gradient(circle 10px, #1976d2 100%, transparent 0),
      radial-gradient(circle 10px, #1976d2 100%, transparent 0),
      radial-gradient(circle 10px, #1976d2 100%, transparent 0),
      radial-gradient(circle 10px, #1976d2 100%, transparent 0),
      radial-gradient(circle 10px, #1976d2 100%, transparent 0);
    background-position: 0em -18em, 0em 18em, 18em 0em, -18em 0em, 13em -13em,
      -13em -13em, 13em 13em, -13em 13em;
    background-repeat: no-repeat;
    font-size: 0.5px;
    border-radius: 50%;
    animation: blast 1s ease-in infinite;
  }

  &__animation::after {
    font-size: 1px;
    background: #0353a30e;
    animation: bounce 1s ease-in infinite;
  }

  @keyframes bounce {
    0%,
    100% {
      font-size: 0.75px;
    }
    50% {
      font-size: 1.5px;
    }
  }
  @keyframes blast {
    0%,
    40% {
      font-size: 0.5px;
    }
    70% {
      opacity: 1;
      font-size: 4px;
    }
    100% {
      font-size: 6px;
      opacity: 0;
    }
  }
}
